@import url(//fonts.googleapis.com/earlyaccess/notosanstc.css);
@import url('https://fonts.googleapis.com/css?family=Crimson+Text:600i|Roboto:500');
html, body{
    font-family: 'Roboto', "Noto Sans TC", "Microsoft JhengHei", "HanHei TC", "MHei", "PingFang TC", "STHeitiTC-Light", "Helvetica Neue", "Helvetica", "Arial", sans-serif;
}
input, textarea , button, submit {
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    -webkit-box-shadow: none;
            box-shadow: none;
    outline: none;
    border: none;
}
textarea {
    resize: none;
}
a {
  color: inherit;
  text-decoration: inherit;

}
ul {
    /* list-style-type: none; */
    margin: 0;
    /* padding: 0; */
}
*{
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    background-repeat: no-repeat;
    background-position: center center;
    margin: 0;
    padding: 0;
}



