.item .tag span{
	font-size: 12pt;
	display: table;
	margin-bottom: 5px;
	padding: 5px 10px;
	position: relative;
	z-index: 10;
}

.item .tag .red-tag{
	color: white;
}

.item .tag .red-tag:after{
	content: '';
	width: 100%;
	height: 100%;
	background-color: #E00442;
	position: absolute;
	left: 0;
	top: 0;
	z-index: -1;
}

.item .tag .white-tag{
	color: #E00442;
}

.item .tag .white-tag:after{
	content: '';
	width: 100%;
	height: 100%;
	background-color: white;
	position: absolute;
	left: 0;
	top: 0;
	z-index: -1;
}

.item .title h2{
	color: white;
	font-size: 30pt;
	font-family: "Crimson Text", serif;
	display: table;
	position: relative;
	z-index: 10;
	padding: 0 10px;
	letter-spacing: 3px;
}

@media (min-width: 768px){

	.item .title h2{
		font-size: 50pt;
	}
}

@media (min-width: 1440px){

	.item .title h2{
		font-size: 75pt;
	}
}

.item .title h2:after{
	content: '';
	width: 100%;
	height: 100%;
	background-color: #E00442;
	padding: 5px 0;
	position: absolute;
	top: -5px;
	left: 0px;
	z-index: -1;
}

